<template>
  <div class="page">
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="账号冻结申诉"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content unfreeze-form">
      <div class="tip">请先验证您对于该账号的所有权</div>
      <label for="phone">
        <div class="title">手机号</div>
        <input type="tel" id="phone" class="input" placeholder="输入手机号码" v-model="form.phone" maxlength="13">
      </label>
      <div class="division"></div>
      <button class="button button-clear btn-get-code"
          :class="{'button-royal disabled': getCodeLock, 'button-dark': !getCodeLock}"
          @click="sendCode()" v-text="getCodeText"></button>
      <div class="bottom-line"></div>
      <label for="code">
        <div class="title">验证码</div>
        <input type="tel" id="code" class="input" placeholder="输入验证码" v-model="form.code">
        <div class="bottom-line last"></div>
      </label>

      <button class="button button-submit" :class="{'can-submit': canSubmit}" @click="submit()">提交申诉</button>
    </div>
  </div>
</template>
<style lang='scss' scoped>
  @import "../../../scss/mixins";

  .unfreeze-form {
    margin-top: 50px;
    padding-left: 20px;
    background: #FFFFFF;

    .tip {
      margin-top: 20px;
      margin-bottom: 20px;
      font-size: 13px;
      height: 18px;
      line-height: 18px;
      color: #b5b5b5;

      @include font-regular();
    }

    .title {
      font-size: 12px;
      height: 17px;
      line-height: 17px;
      color: #b5b5b5;

      @include font-regular();
    }

    .input {
      width: 166px;
      height: 33px;
      font-size: 24px;
      line-height: 33px;
      margin-bottom: 3px;
      display: inline-block;
      padding: 0;

      &::-webkit-input-placeholder {
        line-height: 33px;
        height: 33px;
        color: #DDDDDD;
      }
    }

    .division {
      position: absolute;
      top: 84px;
      right: 116px;
      width: 1px;
      height: 17px;
      background: #DDDDDD;
    }

    .btn-get-code {
      position: absolute;
      padding: 0;
      height: 20px;
      font-size: 14px;
      line-height: 20px;
      min-height: 20px;
      top: 82px;
      right: 30px;
    }

    .bottom-line {
      width: 200px;
      height: 1px;
      background: #DDDDDD;
      margin-bottom: 30px;

      &.last {
        margin-bottom: 60px;
      }
    }

    .button-submit {
      width: 172px;
      height: 40px;
      min-height: 40px;
      line-height: 40px;
      font-size: 13px;
      font-weight: 500;
      color: #FFFFFF;
      margin-bottom: 173px;
      background: #DDDDDD;
      border-radius: 2px;

      @include font-regular();

      &.can-submit {
        background-color: #ef502b;
      }
    }

    .link-agreement {
      height: 20px;
      font-size: 14px;
      color: #b5b5b5;

      span {
        color: #4a90e2;
      }
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index'
  import {popup_choose_verify_code_type, popup_remind_use_correct_mobile, popup_unfreeze_unbind_mobile} from '../popup.js'
  import {mapActions} from 'vuex'

  const countDownLimit = 60
  var formatLock = false

  export default {
    components: {
      AnimateHeader
    },

    data () {
      return {
        form: {
          phone: '',
          code: ''
        },

        getCodeLock: false,
        getCodeText: '获取验证码',
        sec: 0,

        canSubmit: false,
        // 防止二次提交
        submitLock: false,
        voiceVerify: false
      }
    },

    watch: {
      'form.phone': function (val) {
        if (formatLock) return
        formatLock = true
        // 格式化
        let v = val.replace(/\s/g, '')
        this.form.phone = _.filter([v.substr(0, 3), v.substr(3, 4), v.substr(7, 4)], function (value) { return value !== '' }).join(' ')
        formatLock = false
        this.canSubmit = this.canFormSubmit(val, this.form.code)
      },
      'form.code': function (val) {
        this.canSubmit = this.form.phone && val && val.length === 4

        if (val.length > 4) this.form.code = val.substr(0, 4)
        this.canSubmit = this.canFormSubmit(this.form.phone, val)
      }
    },

    methods: {
      ...mapActions('help', [
        'unfreezeSendCode',
        'unfreezeComplain'
      ]),

      submit () {
        if (this.submitLock) return
        this.submitLock = true
        setTimeout(() => { this.submitLock = false }, 1000)

        if (!this.form.phone || !this.form.code) return

        let tips = this.check_input()
        if (tips) {
          $toast.show(tips)
          return
        }

        let phone = this.form.phone.replace(/\s/g, '')
        let code = this.form.code

        this.unfreezeComplain({phone: phone, code: code})
          .then((res) => res.data && res.data)
          .then(data => {
            if (data.err_code === 40003) {
              this.show_correct_mobile(data)
            } else if (data.err_code === 40006) {
              this.show_unfreeze_unbind_mobile()
            } else {
              $router.forward('/help/ComplainUnfreeze')
            }
          })
      },

      sendCode () {
        if (this.voiceVerify) {
          this.popCodeType()
        } else {
          this.getCode(1)
        }
      },

      popCodeType () {
        let popup = $popup.fromTemplate(
          popup_choose_verify_code_type.template,
          popup_choose_verify_code_type.options
        )
        popup.show().then((index) => {
          if (index === 0) {
            this.getCode(1)
          }
          if (index === 1) {
            this.getCode(2)
          }
        })
      },

      getCode (type) {
        // 防止按钮被快速点击两次
        if (this.getCodeLock) return
        this.getCodeLock = true

        // 处于倒计时中
        if (this.sec > 0) return

        // 校验
        let phone = this.form.phone.replace(/\s/g, '')
        let tips = ''

        if (!phone) {
          tips = '手机号不能为空'
        } else if (!/^1[23456789][\d]{9}$/.test(phone)) {
          tips = '手机号格式不正确'
        }

        if (tips) {
          setTimeout(() => { this.getCodeLock = false }, 1000)
          $toast.show(tips)
          return
        }
        this.voiceVerify = true

        this.unfreezeSendCode({phone: phone, type: type})
          .then((res) => res.data && res.data)
          .then(data => {
            if (data.err_code === 40003) {
              this.show_correct_mobile(data)
            } else if (data.err_code === 40006) {
              this.show_unfreeze_unbind_mobile()
            } else {
              this.sec = countDownLimit
              this.countDown()
            }
          })
          .catch(() => {
            this.getCodeLock = false
          })
      },

      show_correct_mobile (data) {
        let mobile = data.err_msg.substr(0, 11).split('')
        mobile.splice(3, 4, '****')
        mobile = mobile.join('')
        let popup = $popup.fromTemplate(
          popup_remind_use_correct_mobile.template(mobile),
          popup_remind_use_correct_mobile.options
        )
        popup.show()
        this.voiceVerify = false
        setTimeout(() => { this.getCodeLock = false }, 1000)
      },

      show_unfreeze_unbind_mobile (data) {
        let popup = $popup.fromTemplate(
          popup_unfreeze_unbind_mobile.template(),
          popup_unfreeze_unbind_mobile.options
        )
        popup.show()
        this.voiceVerify = false
        setTimeout(() => { this.getCodeLock = false }, 1000)
      },

      countDown () {
        if (this.sec) {
          this.sec--
          this.getCodeText = this.sec ? '已发送(' + this.sec + 's)' : '发送验证码'
          setTimeout(() => {
            this.countDown()
          }, 1000)
        } else {
          this.getCodeLock = false
        }
      },

      check_input () {
        let phone = this.form.phone.replace(/\s/g, '')
        let code = this.form.code
        let tips = ''

        if (!phone) {
          tips = '手机号不能为空'
        } else if (!/^1[23456789][\d]{9}$/.test(phone)) {
          tips = '手机号格式不正确'
        } else if (!code) {
          tips = '验证码不能为空'
        } else if (!/^[\d]{4}$/.test(code)) {
          tips = '验证码格式不正确'
        }

        return tips
      },

      bindMoble () {
        $router.forward({
          path: '/user/bindMobile/0',
          query: {
            backroute: '/help/unfreezeForm'
          }
        })
      },

      back () {
        $router.back('/help/complainUnfreeze')
      },

      canFormSubmit (phone, code) {
        return phone && phone.length === 13 && code && code.length === 4
      }
    }
  }
</script>



